import React, { useState, useEffect } from 'react';

const Bestillinger = () => {
  const [bestillinger, setBestillinger] = useState([]);

  useEffect(() => {
    fetch('https://fido-og-fidoline-m-default-rtdb.europe-west1.firebasedatabase.app/produkter.json')
      .then((response) => response.json())
      .then((data) => {
        const bestillingerData = data;
        const bestillingerList = [];
        for (let id in bestillingerData) {
          bestillingerList.push({ id, ...bestillingerData[id] });
        }
        setBestillinger(bestillingerList);
      });
  }, []);

  return (
    <div>
      {bestillinger.map((bestilling) => (
        <div key={bestilling.id}>
          <h2>{bestilling.navn}</h2>
          <p>{bestilling.beskrivelse}</p>
          <p>{bestilling.pris}</p>
        </div>
      ))}
    </div>
  );
};

export default Bestillinger;
