import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Produktkort from "../../components/admin/ProduktAdminkort";

export default function AdminSide() {
    // Posts kommer til at indeholde alle produkter i en liste (array)
    const [posts, setPosts] = useState([]);
    const [isPosts, setIsPosts] = useState(true); // isPosts kan være true eller false. Hvis ingen produkter eksisterer, så er isPosts false. 
    const navigate = useNavigate();

// Funktion (hook) som henter produkter fra Firebase database. 
useEffect(() => {
    async function getPosts() {
    const url = "https://fido-og-fidoline-m-default-rtdb.europe-west1.firebasedatabase.app/produkter.json"; // Ved at tilføje /produkter.json udpejer man, at det er produkter man vil arbejde med.
    const response = await fetch(url);
    // data indeholder al indhol fra produkt database. 
    const data = await response.json();

    // If sætningen betyder "Hvis data er noget andet end nul" (hvis der er produkter)
    if (data !== null) {
        const postsArray = Object.keys(data).map((key) => ({
        id: key,
        ...data[key],
    }));
    // Variablen "posts" bliver lig med listen af produkter 
    setPosts(postsArray);
    } 

    // Sætter variablen isPosts til false, for der er ingen produkter. 
    else {
    setIsPosts(false);

    }
    }

    getPosts();

}, []);

// Event handler for klik på opret knap. Så admin kan oprette et produkt
function opretklik() {
    navigate("/create");
    }

    return (
        <main>
            <h1>
            Administration af produkter <button type="button" onClick={opretklik}>+ Opret produkt</button>
            </h1>
            
            {isPosts ? (
                <div className="kortraekke">
                {posts.map((post) => (
                <Produktkort key={post.id} post={post} />
                ))}
                </div>

            ) : ( 
                
                <p>Ingenting at vise</p>
            )}
        </main>
        );
    }

